.Dropdown-root {
  position: relative;
}

.Dropdown-control {
  box-sizing: border-box;
  color: #333;
  cursor: default;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 2px;
  outline: none;
  padding: 8px 52px 8px 10px;
  transition: all .2s;
  position: relative;
  overflow: hidden;
}

.Dropdown-control:hover {
  box-shadow: 0 1px #0000000f;
}

.Dropdown-arrow {
  content: " ";
  height: 0;
  margin-top: -ceil(2.5);
  width: 0;
  border: 5px solid #0000;
  border-top-color: #999;
  border-bottom-width: 0;
  display: block;
  position: absolute;
  top: 14px;
  right: 10px;
}

.is-open .Dropdown-arrow {
  border-width: 0 5px 5px;
  border-color: #0000 #0000 #999;
}

.Dropdown-menu {
  box-sizing: border-box;
  max-height: 200px;
  width: 100%;
  z-index: 1000;
  -webkit-overflow-scrolling: touch;
  background-color: #fff;
  border: 1px solid #ccc;
  margin-top: -1px;
  position: absolute;
  top: 100%;
  overflow-y: auto;
  box-shadow: 0 1px #0000000f;
}

.Dropdown-menu .Dropdown-group > .Dropdown-title {
  color: #333;
  text-transform: capitalize;
  padding: 8px 10px;
  font-weight: bold;
}

.Dropdown-option {
  box-sizing: border-box;
  color: #333c;
  cursor: pointer;
  padding: 8px 10px;
  display: block;
}

.Dropdown-option:last-child {
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.Dropdown-option:hover, .Dropdown-option.is-selected {
  color: #333;
  background-color: #f2f9fc;
}

.Dropdown-noresults {
  box-sizing: border-box;
  color: #ccc;
  cursor: default;
  padding: 8px 10px;
  display: block;
}

